.tabs {
  padding: 0 1rem; }

.tabs__nav {
  height: 48px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 20px 0; }

.tabs__nav-item:last-child:hover {
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px; }

.tabs__nav-item:first-child:hover {
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px; }

.tabs__nav-item:hover {
  background-color: #f4f4f4; }

.tabs__nav-item {
  color: black;
  text-decoration: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
  position: relative;
  -webkit-transition: all .5s ease-in-out;
  -o-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out; }
  .tabs__nav-item::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 3px;
    -webkit-transition: all .5s ease-in-out;
    -o-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out; }

.tabs__nav-item_active {
  color: #2196F3; }
  .tabs__nav-item_active::before {
    background-color: #2196F3; }

.tabs__content {
  display: -ms-flexbox;
  display: flex; }

.tabs__content-item {
  width: 100%;
  display: none;
  opacity: 0; }

.tabs__content-item_active {
  display: -ms-flexbox;
  display: flex;
  opacity: 1; }

.autocomplete {
  border: none;
  border-bottom: 1px solid #A9A9A9;
  -webkit-transition: all .4s;
  -o-transition: all .4s;
  transition: all .4s; }

.autocomplete:focus {
  border-bottom: 1px solid #2196F3; }



