#location-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  max-width: 1000px;
  min-width: 320px;
  margin: 0 auto;
  padding: .6rem 1rem 1rem 1rem; }

#location-form > div {
  width: 100%;
  max-width: 400px;
  margin: 0 auto; }

.autocomplete {
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 3rem;
